import JSConfetti from "js-confetti";
export const jsConfetti = new JSConfetti();

export const getRandomInt = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min; //최댓값은 제외, 최솟값은 포함
};

export const usernameGenerate = (name) => {
  const front = getRandomInt(1000, 9999).toString();
  const back = Date.now().toString();
  return name + front + back;
};
export const b64DecodeUnicode = (str) => {
  return decodeURIComponent(
    Array.prototype.map
      .call(atob(str), function (c) {
        return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
      })
      .join("")
  );
};

export const addComma = (value) => {
  if (!value) return "0";
  let splitVal = value.toString().split(".");
  splitVal[0] = splitVal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  return splitVal.join(".");
};

